<template>
    <div>
        <el-row>
            <p>long型整数{{longNumber}}---{{other}}</p>
        </el-row>
        <el-button @click="getLongNumber" size="small">请求</el-button>
        <p>日历案例</p>
        <calendar />

    </table>
    </div>
</template>
<script>
import qs from 'qs'
import Calendar from '@/plugins/calendar'
export default {
    components: {
        Calendar
    },
    data() {
        return {
            longNumber: null,
            other: null
        }
    },
    created() {
        this.other = qs.stringify({a: 1239802779292147712})
    },
    methods: {
        getLongNumber() {
            const opt = {
                method: 'get',
                url: '/local/object'
            }
            $ajax.ajax(opt).then(res => {
                this.longNumber = res.data[0].invoiceIdentity
            })
        }
    }
}
</script>