/**
 * 全年日历展示
 * */
import da from "element-ui/src/locale/lang/da";
import ta from "element-ui/src/locale/lang/ta";
import fa from "element-ui/src/locale/lang/fa";

export default {
    props: {
        dayoff: {
            type: Array,
            default: () => []
        },
        year: {
            type: [Number, String],
            default: 2020
        }
    },
    beforeCreate() {
        this.theadTitle = ['周', '一', '二', '三', '四', '五', '六', '日']
        this.theadTitleMap = [-1, 0, 1, 2, 3, 4, 5, 6]
        this.startWeek = 0
    },
    data() {
        return {
            // table: this.createTable(),
        }
    },
    created() {
        this.$nextTick(this.createTable)
    },
    render() {
        return (
            <div ref="cont">
                {/*{this.createTable()}*/}
            </div>
        )
    },
    methods: {
        createTable() {
            const table = document.createElement('table')
            this.createThead(table)
            this.createTBody(table)
            console.log(table, this.$refs)
            this.$refs.cont.appendChild(table)
            return table
        },
        createThead(table) {
            const thead = document.createElement('thead')
            const tr = document.createElement('tr')
            this.theadTitle.forEach((item, index) => {
                const th = document.createElement('th')
                th.setAttribute('data-title-map', this.theadTitleMap[index])
                th.textContent = item
                tr.append(th)
            })
            thead.appendChild(tr)
            table.appendChild(thead)
        },
        createTBody(table) {
            const tbody = document.createElement('tbody')
            // 比如一月份,获取1号是周几然后与表头对应
            let month = 1
            const sumDay = this.getCurMonDay(this.year, month)
            let weekTr
            let isRcordWeek = false
            let isFirstWeek = true
            for (let day = 1; day <= sumDay; day++) {
                let dayStr = day.toString()
                if (day < 10) {
                    dayStr = '0' + day
                }
                const currentDateStr = `${this.year}-${month < 10 ? '0' + month : month}-${dayStr}`
                const weekDay = new Date(currentDateStr).getDay() // 周几
                if (day === 1 || weekDay === 1) {
                    weekTr = document.createElement('tr')
                    weekTr.setAttribute('data-week-num', ++this.startWeek)
                }
                for (let item of this.theadTitleMap) {
                    const td = document.createElement('td')
                    if (item === -1 && !isRcordWeek) {
                        td.textContent = this.startWeek
                        weekTr.appendChild(td)
                        isRcordWeek = true
                        continue
                    }
                    if (weekDay !== item) {
                        // console.log('星期'+weekDay, '星期'+item)
                        console.log(currentDateStr, isFirstWeek)
                        weekTr['data-week-num'] === 1 && (td.textContent = '&nbsp;') && weekTr.appendChild(td)
                        continue
                    }
                    td.textContent = day
                    td.setAttribute('data-date', currentDateStr)
                    // console.log(currentDateStr, '星期'+weekDay)
                    weekTr.appendChild(td)
                }
                isFirstWeek = false
                if (weekDay === 0) {
                    tbody.appendChild(weekTr.cloneNode(true))
                    weekTr = undefined
                    isRcordWeek = false
                }
            }
            table.appendChild(tbody)
        },
        setMonthCalendar(m) {

        },
        getCurMonDay(y, m) {
            if (!y || !m) {
                throw new Error('params: year and month be must!')
            }
            typeof y === 'string' && (y = Number(y))
            if (m === 12) {
                m = 1
                y = y + 1
            }
            const dayTime = 1000 * 60 * 60 * 24
            const nextDay = `${y}-${m < 10 ? '0' + m : m}-01`
            const lastDay = new Date(new Date(nextDay).getTime() - dayTime).getDate()
            return lastDay
        }
    }
}